import './MenuTab.css';
import './common.css';
import { useNavigate } from 'react-router-dom';

//    .
const MenuTab = () => {
  const navigate = useNavigate();

  return (
    <div className="menu-tab">
      <button className="menu-tab-button" onClick={ () => navigate("/") }> </button>
      <button className="menu-tab-button" onClick={ () => navigate("/wholeCountryPage") }> </button>
      <button className="menu-tab-button" onClick={ () => navigate("/bookmarkPage") }></button>
    </div>
  );
}

export default MenuTab;